* {
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
  font-family: Montserrat Alternates, sans-serif;
}

.main {
  background-image: url("main-background.60ab0d77.png");
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  width: 1090px;
  background-color: #fff;
  margin: 0 auto;
  padding: 36px 15px;
}

.header {
  background-color: #fff;
}

.header__positioning-list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__left {
  align-items: center;
  gap: 80px;
  display: flex;
}

.header__right, .header__nav-list {
  align-items: center;
  gap: 50px;
  display: flex;
}

.header__dropdown {
  position: relative;
}

.header__text {
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-size: 12px;
  font-weight: 400;
}

.header__rotate-dropdown {
  transform: rotate(90deg);
}

.header__dropdown-content {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 13px;
  margin-left: -20px;
  display: none;
  position: absolute;
}

.header__dropdown-content-item {
  width: 160px;
  z-index: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: auto;
}

.header__dropdown-content-item:nth-child(3) {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.header__dropdown-link {
  background-color: #fff;
  margin-top: 10px;
  margin-bottom: 10px;
}

.header__input {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header__checkbox {
  opacity: 0;
  width: 0;
  height: 0;
  border-radius: 25px;
}

.header__slider {
  cursor: pointer;
  background-color: #7e7e7e;
  border-radius: 26px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  top: -1px;
}

input:checked + .header__slider:before {
  transform: translateX(18px);
}

.header__show {
  display: block;
}

.test-text {
  font-family: Sacramento, sans-serif;
  font-size: 28px;
  font-weight: bald;
}

.time__calculator_input {
  background-color: #d7d7d7;
  border: none;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 10px;
  font-size: 12px;
  font-weight: 400;
  position: relative;
}

.time__calculator_btn {
  color: #fff;
  background-color: #000;
  border: none;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 10px;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
}

.time__calculator_menu {
  justify-content: space-around;
  align-items: center;
  padding-top: 36px;
  padding-right: 130px;
  display: flex;
}

.time__calculator_text {
  color: #000;
  text-align: center;
  font-size: 12px;
  font-weight: 400;
}

.time__calculator_title_wrap {
  justify-content: center;
  display: flex;
}

.time__calculator_container {
  border-bottom: 1px solid #000;
  padding-top: 36px;
  padding-bottom: 36px;
}

.game {
  width: 600px;
  height: 200px;
  border-bottom: 1px solid #000;
  margin: auto;
}

#dino {
  width: 50px;
  height: 50px;
  background-color: red;
  background-size: 50px 50px;
  position: relative;
  top: 150px;
}

#cactus {
  width: 20px;
  height: 40px;
  background-color: green;
  background-size: 20px 40px;
  animation: cactusMov 1s linear infinite;
  position: relative;
  top: 110px;
  left: 580px;
}

@keyframes cactusMov {
  0% {
    left: 580px;
  }

  100% {
    left: -20px;
  }
}

.jump {
  animation: jump .3s linear;
}

@keyframes jump {
  0% {
    top: 150px;
  }

  30% {
    top: 130px;
  }

  50% {
    top: 80px;
  }

  80% {
    top: 130px;
  }

  100% {
    top: 150px;
  }
}

